<template>
  <div class="index">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <a class="f-small main-c mr5" download href="../gy-files/template/作业检查导入模板.xlsx">下载模板</a>
        <label for="importDom">
          <div class="btn-main btn-upload ml5">导入登记表</div>
        </label>
        <input
            accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
            @change="upload($event)" ref="upload" id="importDom" type="file" hidden>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <div class="table-box">
        <!--<resize-table>-->
        <table>
          <tr>
            <td rowspan="2">学科</td>
            <td rowspan="2">年级</td>
            <td rowspan="2">教师姓名</td>
            <td rowspan="2">班级</td>
            <td rowspan="2">学生姓名</td>
            <td colspan="5">类型一(练习册)</td>
            <td colspan="5">类型二(其它)</td>
            <td colspan="3">总体评价</td>
          </tr>
          <tr>
            <td>批改次数</td>
            <td>有无评价</td>
            <td>有无日期</td>
            <td>有无纠错</td>
            <td>详略批改</td>
            <td>批改次数</td>
            <td>有无评价</td>
            <td>有无日期</td>
            <td>有无纠错</td>
            <td>详略批改</td>
            <td>总等级</td>
            <td>总次数</td>
            <td>亮点与不足</td>
          </tr>
          <tbody v-for="(item,key) in table.list">
            <tr>
              <td :rowspan="item.recordList.length">{{item.courseName}}</td>
              <td :rowspan="item.recordList.length">{{item.gradeName}}</td>
              <td :rowspan="item.recordList.length">{{item.teacherName}}</td>
              <td :rowspan="item.recordList.length">{{item.className}}</td>
              <td>{{item.recordList[0].studentName}}</td>
              <td>{{item.recordList[0].checkNum1}}</td>
              <td>{{item.recordList[0].evaluate1}}</td>
              <td>{{item.recordList[0].date1}}</td>
              <td>{{item.recordList[0].errorRecovery1}}</td>
              <td>{{item.recordList[0].conciseCheck1}}</td>
              <td>{{item.recordList[0].checkNum2}}</td>
              <td>{{item.recordList[0].evaluate2}}</td>
              <td>{{item.recordList[0].date2}}</td>
              <td>{{item.recordList[0].errorRecovery2}}</td>
              <td>{{item.recordList[0].conciseCheck2}}</td>
              <td :rowspan="item.recordList.length" >{{$enums.AssessLevel.getName(item.assessLevel)}}</td>
              <td :rowspan="item.recordList.length" >{{item.assessNum}}</td>
              <td :rowspan="item.recordList.length" >{{item.remark}}</td>
            </tr>
            <tr v-show="k !== 0" v-for="(i,k) in item.recordList">
              <td>{{item.recordList[k].studentName}}</td>
              <td>{{item.recordList[k].checkNum1}}</td>
              <td>{{item.recordList[k].evaluate1}}</td>
              <td>{{item.recordList[k].date1}}</td>
              <td>{{item.recordList[k].errorRecovery1}}</td>
              <td>{{item.recordList[k].conciseCheck1}}</td>
              <td>{{item.recordList[k].checkNum2}}</td>
              <td>{{item.recordList[k].evaluate2}}</td>
              <td>{{item.recordList[k].date2}}</td>
              <td>{{item.recordList[k].errorRecovery2}}</td>
              <td>{{item.recordList[k].conciseCheck2}}</td>
            </tr>
          </tbody>
        </table>
        <!--</resize-table>-->
      </div>
    </main-part>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        nav: [{name: "作业检查", path: ""}],
        searchData: [
          {type: 'select', title: '学期', width: '240px', notRemove: true, model: 'termId', option: []},
          {type: 'select', title: '年级', notRemove: true, model: 'gradeId', option: []},
          {type: 'select', title: '学科', notRemove: true, model: 'courseId', option: []},
        ],
        table: {
          url: '/assessHomework/queryAssessHomeworkPage.do',
          el: '.table-box',
          total: 0,
          noData: true,
          list: [],
          query: {
            termId: null,
            gradeId: null,
            courseId: null
          }
        },
      }
    },
    methods: {
      // 导入
      async upload(e) {
        if (e.target.files.length === 0) return
        let file = e.target.files[0]
        this.$refs.upload.value = null;
        let formData = new FormData()
        formData.append('file', file)
        let obj = {
          url: '/import/importAssessHomework.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if (res.errMsgList && res.errMsgList.length) {
          this.$message.error(res.errMsgList[0])
        } else {
          this.$remind('导入成功')
        }
        this.$search(this.table)
      },
      // 获取学期
      async getTermList() {
        let res = await this.$ajax({
          url: '/school/querySchoolTermList.do'
        })
        this.searchData[0].option = res.map(e => {
          return {label: e.termName, value: e.schoolTermId}
        })
        res.forEach(e=>{
          if(e.isDefault === 'YES'){
            this.table.query.termId = e.schoolTermId
          }
        })
      },
      // 获取年级列表
      async getGradeList() {
        let res = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
        this.searchData[1].option = res.map(e => {
          return {label: e.name, value: e.id}
        })
        this.table.query.gradeId = res[0].id
      },
      // 获取学科
      async subject() {
        let res = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do'
        })
        this.searchData[2].option = res.map(e => {
          return {value: e.id, label: e.name}
        })
        this.table.query.courseId = res[0].id
      },
    },
    async mounted() {
      await this.getTermList()
      await this.getGradeList()
      await this.subject()
      this.$search(this.table)

    }
  }
</script>
